import {isConvertible} from './isConvertible';

/**
 * This is a class that implement a interface
 */
export class Tiempo implements isConvertible {
    constructor(public unidad: number) {

    }
    help() {
        console.log("Cree un objeto con un tiempo en horas y con la función convertir se le devolverá en segundos");
    }

    convertir() {
        return this.unidad * 3600;
    }
}